.header {
    background: linear-gradient(to right, #ff5f6d, #ffc371);
    height: 25px;
    width: 330px !important;
    display: flex;
align-items: center;
justify-content: center;
 margin-bottom: 20px; 
 z-index: 999;
 overflow: hidden;
 border-radius: 2px;
 border: 2px solid grey;
 box-shadow: #ffc371;
}


.moving-text {
color: white;
  font-size: 12px;
  font-family: 'monospace';
  animation: moveText 15s linear infinite;
  white-space: nowrap;
  
}

@keyframes moveText {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
}
@media (min-width:1225px){

    .header {
        background: linear-gradient(to right, #ff5f6d, #ffc371);
        height: 35px;
        display: flex;
        width: auto !important;
    align-items: center;
    justify-content: center;
     margin-bottom: 100px; 
     z-index: 999;
     overflow: hidden;
  }
  

.moving-text {
    color: white;
      font-size: 16px;
      animation: moveText 20s linear infinite;
      white-space: nowrap;
      
    }
    
    @keyframes moveText {
        0% {
            transform: translateX(100%);
        }
        100% {
            transform: translateX(-100%);
        }
    }
}