/* === new === */
#root{
  overflow-x: hidden;
}
.banner-wrap{
  margin-top: 60px;
  height: 92vh;
  background-image: linear-gradient(50deg, rgba(187, 4, 141, 0.568), rgba(105, 5, 89, 0.774));
  background-color: rgb(168, 2, 2);
  background-size: cover;
  padding: 0 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  overflow: hidden;
}
.banner-wrap .banner-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.banner-wrap .banner-content .col{
  width: 50%;
}
.banner-wrap .banner-content .banner-text p, .banner-wrap .banner-content .banner-text h2{
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
}
.logo-square{
  width: 45px;
  height: 45px;
  margin: 0 10px;
  border-radius: 2px;
  box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.2);
}
.banner-wrap .banner-content .banner-text h2{
  font-size: 50px;
  font-weight: 700;
}
.banner-wrap .banner-content .banner-text span{
  display: block;
  padding-bottom: 0;
  font-family: 'Raleway', sans-serif;
}
.banner-wrap .banner-content .banner-text a{
  display: inline-block;
  width: 80px;
  height: 80px;
  background-position: center;
  background-size: cover;
  margin: 0 5px;
}
.banner-wrap .banner-content .banner-text a span{
  display: none;
}
.banner-wrap .banner-content .banner-text .btn-zomato{
  background-image: url(https://www.lunchsquare.in/images/zomato.png);
}
.banner-wrap .banner-content .banner-text .btn-swiggy{
  background-image: url(https://www.lunchsquare.in/images/swiggy.png);
}
.banner-wrap .banner-content .banner-text .btn-whatsapp-wrap{
  margin-top: 30px;
}
.banner-wrap .banner-content .banner-text .btn-whatsapp-wrap .btn-whatsapp{
  background: linear-gradient(170deg, #3bdf6c, #085a21);
  background-color: #31c55d;
  color: #ebebeb;
  border-radius: 50px;
  padding: 6px 45px;
  width: auto!important;
  height: auto;
  text-decoration: none;
  border: 1px solid #ffffffab;
  position: relative;
  z-index: 999;
}
.banner-wrap .banner-content .banner-text .btn-whatsapp-wrap .btn-whatsapp img{
  width: 25px;
  height: 25px;
}
.banner-wrap .banner-content .banner-text .btn-whatsapp-wrap .btn-whatsapp:hover{
  transition: 0.7s ease;
  background: linear-gradient(50deg, #3bdf6c, #085a21) #31c55d;
}
.banner-wrap .banner-content .img-container{
  width: 70%;
  rotate: -40deg;
  overflow: hidden;
}
.banner-wrap .banner-content .img-container img{
  width: 100%;
  height: 100%;
}

@media (max-width: 780px){
  .banner-wrap{
    flex-direction: column;
    align-items: flex-start;
    padding: 0 20px;
  }
  .banner-wrap .banner-content{
    display: block;
  }
  .banner-wrap .banner-content .col{
    width: 100%;
  }
  .banner-wrap .banner-content .img-container{
    width: 50%;
    margin: 0 auto;
    float: right;
    margin-top: -90px;
  }
  .banner-wrap .banner-content .banner-text h2{
    font-size: 30px;
  }
}
@media (max-width: 450px){
  .banner-wrap .banner-btns{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 999;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .btn-whatsapp-wrap{
    
    height: 100px;
    
    top: 500px; 
  }
  .banner-wrap .banner-btns a{
    width: 50%!important;
    margin: 0!important;
    text-align: center;
    text-decoration: none;
    height: initial!important;
    padding: 6px 0!important;
  }
  .banner-wrap .banner-btns a span{
    display: block!important;
    color: #fff;
    font-size: 30px;
  }
  .banner-wrap .banner-btns .btn-zomato{
    background-image: none!important;
    background: #ED1F25;
  }
  .banner-wrap .banner-btns .btn-swiggy{
    background-image: none!important;
    background: #F58722;
  }
}
/* === filters === */
.filters{
  padding: 15px 35px;
  margin-top: 45px;
}
.filters h4{
  text-align: left!important;
  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  font-weight: 600;
}
.filters .btn-danger{
  margin-top: 30px;
}
.filters .btn-danger, .btn-global{
  background: linear-gradient(50deg, rgba(187, 4, 141, 0.568), rgba(105, 5, 89, 0.774));
  background-color: rgb(168, 2, 2);
  color: #fff;
  font-family: 'Raleway', sans-serif;
  border-radius: 3px;
  border: none;
  transition: 0.3s ease;
}
.btn-global:hover{
  /* background-color: rgb(4, 60, 133); */
  background: linear-gradient(70deg, rgba(187, 4, 141, 0.568), rgba(105, 5, 89, 0.774));
  color: #fff;
}

/* === product list === */
.product-list-col h1{
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  padding-bottom: 25px;
}
.product-list-col .product-container{
  text-align: left;
}
.product-list-col .product-container .card{
  width: 300px;
  box-shadow: 0px 0px 10px 3px rgba(4, 89, 187, 0.171);
  border-radius: 2px;
  transition: 0.3s ease;
  display: inline-block;
  vertical-align: top;
  text-align: left;
}
.product-list-col .product-container .card:hover{
  box-shadow: 0px 0px 10px 4px rgba(4, 120, 187, 0.404);
}
.product-list-col .product-container .card .product-img-wrap{
  width: 100%;
  height: 240px;
  overflow: hidden;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.product-list-col .product-container .card img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s ease;
}
.product-list-col .product-container .card img:hover{
  scale: 1.03;
}
.product-list-col .product-container .card .btn-info{
  border-radius: 3px;
  font-family: 'Raleway', sans-serif;
}
@media (max-width: 770px){
  .filters{
    margin: 10px auto 10px auto;
    width: 50%;
  }
  .filters h4{
    font-size: 16px!important;
  }
  .product-list-col .product-container{
    text-align: center!important;
  }
}
@media (max-width: 450px){
  .filters{
    width: 80%;
  }
}