@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

/* === header === */
.navbar .navbar-nav {
    font-family: 'Raleway', sans-serif;
    text-transform: capitalize;
}
.navbar .container-fluid{
    flex-wrap: nowrap;
}
.navbar .logo-square{
    width: 35px;
    height: 35px;
    margin: 0 10px;
    border-radius: 2px;
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.2);
    object-fit: cover;
}
.navbar .navbar-brand{
    font-size: 15px!important;
}
@media (max-width: 992px){
    .navbar-collapse{
        background-color: aliceblue;
        padding: 10px 15px;
        position: absolute;
        right: 10px;
        top: 60px;
    }
}
/* === footer === */
.footer{
    background: linear-gradient(50deg, rgba(3, 45, 70, 0.904), rgba(231, 231, 231, 0.596));
    background-color: rgb(2, 74, 168);
    font-family: 'Raleway', sans-serif;
    padding: 5px 30px; 
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* position: absolute;
    bottom: 0;
    left: 0;
    width: 100%; */
}
.footer h2{
    font-size: 15px;
    margin: 0;
    padding: 0;
}